<template>
    <top-bar
        :user="user"
        @login="user = $event"
        @register="user = $event"
        @logout="user = { class: 'guest' }"
    />
    <search-bar @submit-search="search($event)">
        <author-tool
            :large="true"
            :toggle-search="true"
            :toggle-count="true"
            :toggle-add="user.class !== 'guest' && user.class !== 'user'"
            :toggle-delete="user.class !== 'guest' && user.class !== 'user'"
            @authors-changed="authors = $event"
        />
        <tag-tool
            :large="true"
            :toggle-search="true"
            :toggle-count="true"
            :toggle-add="user.class !== 'guest' && user.class !== 'user'"
            :toggle-delete="user.class !== 'guest' && user.class !== 'user'"
        />
    </search-bar>

    <pagination :total="totalPages" @page-updated="currentPage = $event" />
    <div class="fallback" v-if="fallback.trim().length > 0">
        <h1>{{ fallback }}</h1>
    </div>
    <div class="container" v-else>
        <book-item
            v-for="(book, idx) in books"
            :key="idx"
            :bookdata="book"
            :userclass="user.class"
        />
    </div>
</template>

<script>
import BookItem from "./components/BookItem.vue";
import TopBar from "./components/topBar/TopBar";
import SearchBar from "./components/Searchbar";
import TagTool from "./components/TagTool";
import AuthorTool from "./components/AuthorTool";
import Pagination from "./components/Pagination";

export default {
    name: "App",
    components: {
        BookItem,
        TopBar,
        SearchBar,
        TagTool,
        AuthorTool,
        Pagination,
    },
    data() {
        return {
            fallback: "loading",
            user: {
                class: "guest",
            },
            books: [],
            tags: [
                {
                    tid: 1,
                    name: "1",
                    bcount: 233,
                },
            ],
            authors: [],
            totalPages: 1,
            currentPage: 1,
            searchText:'',
            filter_expanded: false,
        };
    },
    created() {
        this.checkLogin();
        this.fetchData();
        // this.fetchuser();
    },
    methods: {
        fetchData() {
            this.fallback = "Loading";
            fetch("/api/book/list", {
                method: "GET",
            })
                .then((res) => res.json())
                .then((res) => {
                    if (res.ok) {
                        this.books = res.data.books;
                        this.totalPages = res.data.count;
                        // console.log(res.data);
                        this.fallback = "";
                    } else {
                        alert("failed");
                        console.log(res);
                    }
                })
                .catch((err) => {
                    console.log(err);
                    this.fallback = JSON.stringify(err);
                });
            fetch("/api/tag/list", {
                method: "GET",
            })
                .then((res) => res.json())
                .then((res) => {
                    if (res.ok) {
                        this.tags = res.data;
                        // console.log(res.data);
                    } else {
                        alert("failed");
                        console.log(res);
                    }
                })
                .catch((err) => console.log(err));
        },
        checkLogin() {
            fetch("/user/login", {
                method: "POST",
            })
                .then((res) => res.json())
                .then((res) => {
                    if (res.ok) {
                        this.user = res.data;
                    }
                })
                .catch((err) => console.log(err));
        },
        search(searchText) {
            // var data = new FormData();
            // data.append('s_text', searchText);
            // data.append('tags', this.tags.filter(e => e.active).map(e => e.tid));
            // data.append('authors', this.authors.map(e => e.aid));
            // for (let [key, val] of data.entries()) {
            //     console.log(key, val);
            // }
            if (searchText) this.searchText = searchText;
            else searchText = this.searchText;
            var url = new URL("/api/book/list", window.location.origin);
            var params = {
                authors: JSON.stringify(this.authors.map((e) => e.aid)),
                tags: JSON.stringify(
                    this.tags.filter((e) => e.active).map((e) => e.tid)
                ),
                s_text: searchText,
                pno: this.currentPage,
            };
            url.search = new URLSearchParams(params);

            this.fallback = "Loading";
            fetch(url, {
                method: "GET",
            })
                .then((res) => res.json())
                .then((res) => {
                    if (res.ok) {
                        this.books = res.data.books;
                        this.totalPages = parseInt(res.data.count);
                        console.log(this.totalPages);   
                        // console.log(res.data);
                        this.fallback = "";
                    } else {
                        alert("failed");
                        console.log(res);
                    }
                })
                .catch((err) => {
                    console.log(err);
                    this.fallback = JSON.stringify(err);
                });
        },
    },
    watch: {
        currentPage() {
            this.search();
        }
    }
};
</script>

