<template>
    <modal :show-modal="show" class="large" :title="title" >
        <div class="container-userprofile">
            <table>
                <tbody>
                    <tr>
                        <td>UID</td>
                        <td>{{ user.uid }}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>{{ user.name }}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>{{ user.email }}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>{{ user.count }}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>{{ user.class }}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </modal>
</template>

<script>
import Modal from "../Modal";

export default {
    name: "Userprofile",
    props: {
        show: Boolean,
    },
    components: {
        Modal,
    },
    data() {
        return {
            user: {
                uid:233,
                name:'test',
                email:'233@233.233',
                count:'233',
                class:'test'
            },
            title: "Loading",
        };
    },
    created() {
        this.fetchData();
    },
    watch: {
        show(val) {
            if (val === true) {
                this.fetchData();
            } else this.title = "Loading";
        },
    },
    methods: {
        fetchData() {
            fetch("/api/user", {
                method: "GET",
            })
                .then((res) => res.json())
                .then((res) => {
                    if (res.ok) {
                        this.user = res.data;
                        this.title = `${res.data.name} `;
                    }
                })
                .catch((err) => console.log(err));
        },
    },
};
</script>

<style lang="scss" scoped>
.container-userprofile {
    // position: relative;
    overflow: auto;

    table {
        
        background: #7c6c3769;
        border-radius: 0.25em;
        border-collapse: collapse;
        margin: auto;
        white-space: nowrap;
        min-width: 100%;
        // &::-webkit-scrollbar {
        //     width: 1px;
        // }

        // &::-webkit-scrollbar-track {
        //     box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.3);
        // }

        // &::-webkit-scrollbar-thumb {
        //     background-color: rgba(245, 211, 117, 0.822);
        //     outline: 1px solid rgba(128, 224, 248, 0.219);
        // }
        tbody {
            tr {
                transition: background 0.25s ease;
                &:hover {
                    background: #5554018c;
                }
            }
        }
    }
    th {
        border-bottom: 1px solid #364043;
        color: #e2b842;
        font-size: 0.85em;
        font-weight: 600;
        padding: 0.5em 1em;
        text-align: left;
    }
    td {
        color: #fff;
        font-weight: 400;
        padding: 0.65em 1em;
        &:first-child {
            text-align: right;
            color: black;
        }
        &:last-child {
            text-align: left;
        }
    }
    .disabled td {
        color: #4f5f64;
    }
}
</style>